/*
As it is right now `index.ts` is dedicated to one thing only:

    Changing the navbar color based on which section the user is looking at.

Are there no CSS-only solutions?

    One viable CSS-only solution could be to add `backdrop-filter: blur(64px);`
    to the navbar. It creates a sort of "frosted glass" effect as the navbar
    is on top of the different sections. There are three downsides:
    1) The forsted glass effect can be a little annoying. For instance, the
       navbar turns white-ish when positioned over one of the white stripes.
    2) With the current layout, where the navbar is neither
      `position: absolute` nor `position: sticky`, the navbar is actually never
       overlapping with the sections.
    3) Quick testing on Firefox revealed that `backdrop-filter` didn't play nice
       with our implementation of the mobile dropdown menue (implemented using
       `transform: translateY(..)` and `transform: translateZ(..)`)
    Downside (1) is the only downside where I cannot see a solution. Downside
    (2) and (3) simply mean that the implementation might be a litte complex
    (like the JS solution below)

How does this work?

    We keep track of which sections are visible in the `sectionVisibillity`
    variable (more on HOW we keep track of that later), and simply assume
    that if start is visible, the navbar should have it's background color.
    If start is not visible, but join is, then is should have join
    background color, etc.

    There are edge-cases that are not handled: What if no section appear to be
    visible? What if start and why are visible, but not why??

    We keep track of which sections are visible using an `IntersectionObserver`
    which is an interface designed for keeping track of just these things in
    a very performant manner.
 */

function main() {

  /*
    Get all the relevant elements from the DOM
  */

  const mainTag: HTMLElement = (() => {
    const elem = document.getElementsByTagName("main")
    if (elem.length > 1) {
      throw new Error("Expected one <main> tag only")
    }
    if (elem.length === 0) {
      throw new Error("Expected there to be a <main> tag")
    }

    return elem[0];
  })()

  const navBar: HTMLElement = (() => {
    const elem = document.getElementsByTagName("nav")
    if (elem.length > 1) {
      throw new Error("Expected one <nav> tag only")
    }
    if (elem.length === 0) {
      throw new Error("Expected there to be a <nav> tag")
    }

    return elem[0];
  })()

  const sectionStart: HTMLElement = (() => {
    const elem = document.getElementById("start")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #start tag")
  })()

  const sectionJoin: HTMLElement = (() => {
    const elem = document.getElementById("join")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #join tag")
  })()

  const sectionHelpOut: HTMLElement = (() => {
    const elem = document.getElementById("help-out")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #join tag")
  })()

  const sectionWhy: HTMLElement = (() => {
    const elem = document.getElementById("why")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #why tag")
  })()

  const sectionFAQ: HTMLElement = (() => {
    const elem = document.getElementById("faq")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #faq tag")
  })()

  const sectionContact: HTMLElement = (() => {
    const elem = document.getElementById("contact")
    if (elem instanceof HTMLElement) {
      return elem
    }

    throw new Error("Expected there to be a #contact tag")
  })()

  // Keep track of which section are visible.
  const sectionVisibillity: Visibility = initialVisibillity()

  // Look at `sectionVisibillity` and update navbar bg color accordingly.
  function updateNavbarColor() {
    if (sectionVisibillity["start"] === true) {
      navBar.style.backgroundColor = "var(--green)"
      return
    }
    if (sectionVisibillity["join"] === true) {
      navBar.style.backgroundColor = "var(--blue)"
      return
    }
    if (sectionVisibillity["help-out"] === true) {
      navBar.style.backgroundColor = "var(--green)"
      return
    }
    if (sectionVisibillity["why"] === true) {
      navBar.style.backgroundColor = "var(--blue)"
      return
    }
    if (sectionVisibillity["faq"] === true) {
      navBar.style.backgroundColor = "var(--green)"
      return
    }
    if (sectionVisibillity["contact"] === true) {
      navBar.style.backgroundColor = "var(--gray)"
      return
    }
    // if (sectionVisibillity["section-6"] === true) {
    //   navBar.style.backgroundColor = "var(--red)"
    //   return
    // }
    // if (sectionVisibillity["section-7"] === true) {
    //   navBar.style.backgroundColor = "var(--green)"
    //   return
    // }
    // if (sectionVisibillity["section-8"] === true) {
    //   navBar.style.backgroundColor = "var(--blue)"
    //   return
    // }

    throw new Error("Expected at least on section to be visible")
  }

  // Keep track of which section are visible
  const sectionIntersectionObserver = new IntersectionObserver(events => {
    for (const event of events) {
      if (![
        "start",
        "join",
        "help-out",
        "why",
        "faq",
        "contact",
        // "section-6",
        // "section-7",
        // "section-8"
      ].includes(event.target.id)) {
        throw new Error(
          `Unexpected id in intersection observer callback: "${event.target.id}"`)
      }

      sectionVisibillity[event.target.id] = event.isIntersecting
    }

    updateNavbarColor()
  }, {
    // Why these options: When programatically scrolling to a section it can be
    // the case that while a section is totaly not vissible to a user, it is
    // still technically in the viewport as far as the intersection observer is
    // concearned. By setting rootMargin: "-1px" we basically tell the
    // interesction observer that a section needs to be at least one full pixel
    // in the viewport to count as vissible.
    root: mainTag,
    rootMargin: "-1px"
  })

  sectionIntersectionObserver.observe(sectionStart)
  sectionIntersectionObserver.observe(sectionJoin)
  sectionIntersectionObserver.observe(sectionHelpOut)
  sectionIntersectionObserver.observe(sectionWhy)
  sectionIntersectionObserver.observe(sectionFAQ)
  sectionIntersectionObserver.observe(sectionContact)
  // sectionIntersectionObserver.observe(section6)
  // sectionIntersectionObserver.observe(section7)
  // sectionIntersectionObserver.observe(section8)

  /*
  Why is this here??

  It's a little complicated, but basically, when loading the page for the first
  time we want the color of the navbar to update to the right color ASAP,

  after that, when scrolling, we might want a subtle transition. If we add the
  transition in the CSS from the beginning then it will apply to the above case
  when we want to change color ASAP. So we set the style hare instead, AFTER
  the navbar has gotten its initial color.
  */
  setTimeout(() => { navBar.style.transition = "background-color 0.3s" }, 1000)
}

type Visibility = {
  "start": boolean,
  "join": boolean,
  "help-out": boolean,
  "why": boolean,
  "faq": boolean,
  "contact": boolean,
  // "section-6": boolean,
  // "section-7": boolean,
  // "section-8": boolean,
}

function initialVisibillity(): Visibility {
  return {
    "start": true,
    "join": false,
    "help-out": false,
    "why": false,
    "faq": false,
    "contact": false,
    // "section-6": false,
    // "section-7": false,
    // "section-8": false,
  }
}

if (document.readyState === "complete") {
  main()
} else {
  document.addEventListener("DOMContentLoaded", main)
}
